.nav {
    margin-bottom: 50px;
}

.list {
    position: relative;

    display: flex;
    justify-content: space-between;

    max-width: 400px;
    margin: 0 auto;

    &::before {
        content: "";
        position: absolute;
        left: 5px;
        right: 5px;
        top: 50%;
        height: 1px;
        background-color: rgba(223, 223, 237, 0.8);
        z-index: -1;
    }
}

.item {
    // width: 30px;
    // height: 30px;
}

.link {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;


    width: 35px;
    height: 30px;

    background-color: var(--color-text-invert);
    border: 1px solid #dfdfed;
}

.activeLink {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    gap: 5px;


    width: 35px;
    height: 30px;

    color: var(--color-text-invert);
    background-color: var(--color-text);

    border: 1px solid #dfdfed;
}

.activeLink + span {
    color: var(--color-text);
}

.labelNumber {
    display: flex;
    justify-content: center;
    align-items: center;
}

.labelText {
    position: absolute;
    top: 40px;

    color: var(--color-text);
}